<template>
    <div>
       <mavon-editor v-model="value" :ishljs="true" :toolbars="toolbars" @imgAdd="imgAdd" ref="md" codeStyle="androidstudio" @save="ctrlSave"/>
    </div>
</template>
<script>
import axios from 'axios';

export default {
    props : ['initValue'],
    data(){
    	return {
            value : '',
            toolbars: {
              bold: true, // 
              italic: true, // 
              header: true, // 
              underline: true, // 
              strikethrough: true, // 
              mark: false, // 
              superscript: true, // 
              subscript: true, // 
              quote: true, // 
              ol: true, // 
              ul: true, // 
              link: true, // 
              imagelink: true, // 
              code: true, // code
              table: true, // 
              fullscreen: true, // 
              readmodel: true, // 
              htmlcode: false, // html
              help: true, // 
              /* 1.3.5 */
              undo: true, // 
              redo: true, // 
              trash: false, // 
              save: false, // eventssave
              /* 1.4.2 */
              navigation: true, // 
              /* 2.1.8 */
              alignleft: true, // 
              aligncenter: true, // 
              alignright: false, // 
              /* 2.2.1 */
              subfield: false, // 
              preview: true, // 
              fontSize : '11px',
            }
    	}
    },
    watch : {
        value : function(val) {
            this.$emit('inputMarkdown', val);
        },
        initValue : function(val) {
            this.value = val;
        }
    },
    methods : {
        async imgAdd(pos, $file) {
            let res = await this.$api.uploadImage($file);
            if(res.code == 0) {
                this.$refs.md.$img2Url(pos, "/markdown/image/" + res.file);
            }
            
        },
        ctrlSave() {
          this.$emit('ctrlSave');
        }
    }
}
</script>